/** ==============================
 *  Footer styles.
 *  =============================*/
.footer {
	padding: 0.5em;
	font-size: 0.9em;
	color: white;
	background: -webkit-gradient(linear, left top, right top, from(rgb(89, 89, 89)), color-stop(10%, rgb(102, 102, 102)), color-stop(rgb(115, 115, 115)), color-stop(90%, rgb(102, 102, 102)), to(rgb(89, 89, 89)));
	background: -webkit-linear-gradient(left, rgb(89, 89, 89), rgb(102, 102, 102) 10%, rgb(115, 115, 115), rgb(102, 102, 102) 90%, rgb(89, 89, 89));
	background: -o-linear-gradient(left, rgb(89, 89, 89), rgb(102, 102, 102) 10%, rgb(115, 115, 115), rgb(102, 102, 102) 90%, rgb(89, 89, 89));
	background: linear-gradient(90deg, rgb(89, 89, 89), rgb(102, 102, 102) 10%, rgb(115, 115, 115), rgb(102, 102, 102) 90%, rgb(89, 89, 89));
	-webkit-box-shadow: 0 -1px 16px 1px grey;
	        box-shadow: 0 -1px 16px 1px grey;
	-ms-flex-negative: 0;
	    flex-shrink: 0;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.footer > * {
	margin: 0 0.5em;
}

.footer_copy {
	-ms-flex-order: -1;
	    order: -1;
}

.footer_copy a {
	position: relative;
	color: black;
	text-decoration: none;
	-webkit-transition: color 0.25s linear;
	-o-transition: color 0.25s linear;
	transition: color 0.25s linear;
}

.footer_copy a:hover, .footer_copy a:focus {
	color: white;
}

.footer_copy a::after {
	border-bottom: 1px solid white;
}

.footer_social {
	text-align: center;
}

.social_list {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	-ms-flex-align: center;
	    align-items: center;
	padding: 0.4em 0 0.1em;
}

.social_list a {
	border-radius: 50%;
	padding: 0.2em 0.3em;
	color: white;
	font-size: 1.5em;
	-webkit-transition: color 0.25s linear, background-color 0.25s linear, text-shadow 0.25s linear;
	-o-transition: color 0.25s linear, background-color 0.25s linear, text-shadow 0.25s linear;
	transition: color 0.25s linear, background-color 0.25s linear, text-shadow 0.25s linear;
}

.social_list a:hover, .social_list a:focus {
	background-color: white;
	text-shadow: 4px 4px 10px grey;
}

.social_list a[href*="linkedin"]:hover, .social_list a[href*="linkedin"]:focus {
	color: #0077B5;
}

.social_list a[href*="github"]:hover, .social_list a[href*="github"]:focus {
	color: #000;
}

.social_list a[href*="freecodecamp"]:hover, .social_list a[href*="freecodecamp"]:focus {
	color: #006400;
}

/* Small screen styles */
@media screen and (max-width: 540px) {
	.footer {
		-ms-flex-flow: column nowrap;
		    flex-flow: column nowrap;
	}

	.footer_copy {
		-ms-flex-order: 0;
		    order: 0;
	}
}



