/** ==============================
 *  Header styles.
 *  =============================*/
.header {
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	height: 80px;
	background: -webkit-gradient(linear, left top, right top, color-stop(5%, rgb(0, 153, 204)), color-stop(15%, rgb(0, 172, 230)), color-stop(80%, rgb(0, 191, 255)), color-stop(90%, rgb(0, 172, 230)), color-stop(98%, rgb(0, 153, 204)));
	background: -webkit-linear-gradient(left, rgb(0, 153, 204) 5%, rgb(0, 172, 230) 15%, rgb(0, 191, 255) 80%, rgb(0, 172, 230) 90%, rgb(0, 153, 204) 98%);
	background: -o-linear-gradient(left, rgb(0, 153, 204) 5%, rgb(0, 172, 230) 15%, rgb(0, 191, 255) 80%, rgb(0, 172, 230) 90%, rgb(0, 153, 204) 98%);
	background: linear-gradient(90deg, rgb(0, 153, 204) 5%, rgb(0, 172, 230) 15%, rgb(0, 191, 255) 80%, rgb(0, 172, 230) 90%, rgb(0, 153, 204) 98%);
	-webkit-box-shadow: 0 1px 20px 1px rgb(0, 172, 230);
	        box-shadow: 0 1px 20px 1px rgb(0, 172, 230);
	color: white;
	z-index: 10; /* Stay on top of other elements */
}

.header_container {
	height: 100%;
	max-width: 980px;
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
}

/* Heading styles */
.header_heading {
	margin-left: 1em;
}

.header_heading p {
	font-size: 0.95em;
	margin-bottom: 0.5em;
}

/* Navigation styles */
.navlink_container {
	margin-right: 1em;
	-ms-flex-item-align: end;
	    align-self: flex-end;
}

.navlink_list {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row nowrap;
	    flex-flow: row nowrap;
}

.navlink_item {
	text-align: center;
}

.navlink_item a {
	color: white;
	display: block;
	padding: 0.4em 0.3em;
	text-decoration: none;
	font-weight: 500;
	font-size: 1.1em;
	letter-spacing: 0.2px;
	-webkit-transition: background-color 0.25s linear;
	-o-transition: background-color 0.25s linear;
	transition: background-color 0.25s linear;
	border-radius: 12px 12px 0 0;

	position: relative; /* For nice smooth bottom border animation */
}

.navlink_item a:hover, .navlink_item a:focus, .navlink_item .active, .hamburger_button:hover, .hamburger_button:focus {
	background-color: rgba(0, 0, 0, 0.07);
}

/* Add the bottom border color */
.navlink_item a::after {
	border-bottom: 2px solid white;
}

/* Add an arrow to the bottom of the active navlink */
.navlink_item .active::after {
	width: 0;
	-webkit-transform: scaleX(1);
	    -ms-transform: scaleX(1);
	        transform: scaleX(1);
	-webkit-transition: width 0.25s linear, left 0.25s linear, border 0.15s linear;
	-o-transition: width 0.25s linear, left 0.25s linear, border 0.15s linear;
	transition: width 0.25s linear, left 0.25s linear, border 0.15s linear;

	content: ' ';
	position: absolute;
	left: 43%;
	border: 6px solid;
	border-color: transparent transparent white transparent;
}

.hamburger_container {
	display: none;
	-ms-flex-item-align: end;
	    align-self: flex-end;
	margin-right: 1em;
}

.hamburger_button {
	padding: 0.25em;
	font-size: 1.5em;
	color: white;
	border: none;
	cursor: pointer;
	background: inherit;
	-webkit-transition: background-color 0.25s linear;
	-o-transition: background-color 0.25s linear;
	transition: background-color 0.25s linear;
	border-radius: 12px 12px 0 0;
	width: 4.3em;
}

/* Medium screen styles */
@media screen and (max-width: 815px) {
	.header_container {
		position: relative;
	}

	.navlink_container {
		position: absolute;
		background: -webkit-gradient(linear, left top, right top, from(rgb(89, 89, 89)), color-stop(10%, rgb(102, 102, 102)), color-stop(rgb(115, 115, 115)), color-stop(90%, rgb(102, 102, 102)), to(rgb(89, 89, 89)));
		background: -webkit-linear-gradient(left, rgb(89, 89, 89), rgb(102, 102, 102) 10%, rgb(115, 115, 115), rgb(102, 102, 102) 90%, rgb(89, 89, 89));
		background: -o-linear-gradient(left, rgb(89, 89, 89), rgb(102, 102, 102) 10%, rgb(115, 115, 115), rgb(102, 102, 102) 90%, rgb(89, 89, 89));
		background: linear-gradient(90deg, rgb(89, 89, 89), rgb(102, 102, 102) 10%, rgb(115, 115, 115), rgb(102, 102, 102) 90%, rgb(89, 89, 89));
		-webkit-box-shadow: 0 4px 10px 1px grey;
		        box-shadow: 0 4px 10px 1px grey;
		width: 100%;
		z-index: 1;
		visibility: hidden;
		opacity: 0;
		-webkit-transform: translate(0, 100%) scaleY(0);
		    -ms-transform: translate(0, 100%) scaleY(0);
		        transform: translate(0, 100%) scaleY(0);
		-webkit-transition: opacity 0.2s linear, visibility 0.2s linear, -webkit-transform 0.2s linear;
		transition: opacity 0.2s linear, visibility 0.2s linear, -webkit-transform 0.2s linear;
		-o-transition: opacity 0.2s linear, visibility 0.2s linear, transform 0.2s linear;
		transition: opacity 0.2s linear, visibility 0.2s linear, transform 0.2s linear;
		transition: opacity 0.2s linear, visibility 0.2s linear, transform 0.2s linear, -webkit-transform 0.2s linear;
	}

	.navlink_container.open {
		visibility: visible;
		opacity: 1;
		-webkit-transform: translate(0, 100%) scaleY(1);
		    -ms-transform: translate(0, 100%) scaleY(1);
		        transform: translate(0, 100%) scaleY(1);
	}

	.navlink_list {
		-ms-flex-pack: end;
		    justify-content: flex-end; /* Display the item on the right */
		margin-right: 1em;
	}

	.navlink_item a {
		border-radius: 0;
	}

	.navlink_item a:hover, .navlink_item a:focus, .navlink_item .active {
		background-color: rgba(0, 191, 255, 0.8);
	}

	.hamburger_container {
		display: block; /* Display the hamburger button */
	}
}

/* Small screen styles */
@media screen and (max-width: 550px) {
	.header_container {
		font-size: 0.9em;
	}

	.header_heading p {
		display: none; /* Hide the job title */
	}

	.navlink_container {
		max-height: 0;
		-webkit-transform: translate(0, 100%);
		    -ms-transform: translate(0, 100%);
		        transform: translate(0, 100%);
		-webkit-transition: max-height 0.3s linear, opacity 0.2s linear, visibility 0.2s linear;
		-o-transition: max-height 0.3s linear, opacity 0.2s linear, visibility 0.2s linear;
		transition: max-height 0.3s linear, opacity 0.2s linear, visibility 0.2s linear;
	}

	.navlink_container.open {
		max-height: 400px;
		-webkit-transform: translate(0, 100%);
		    -ms-transform: translate(0, 100%);
		        transform: translate(0, 100%);
	}

	.navlink_list {
		-ms-flex-flow: column nowrap;
		    flex-flow: column nowrap;
		margin: 0;
	}

	.navlink_item a::after{
		width: 60%;
		left: 20%;
	}

	/* Change the arrow of the active navlink to the left */
	.navlink_item .active::after {
		left: 20%;
		bottom: 40%;
		border-color: transparent transparent transparent white;
	}
}



